.dialog{position:fixed;z-index:100;left:0;top:0;width:100%;height:100vh;background-color:rgba(0,0,0,.75);display:flex;justify-content:center;align-items:center;cursor:pointer}.dialog.pure>.dialog__inner{padding:0}.dialog>.dialog__inner{position:relative;background:#fff;max-width:100vw;max-height:100vh;overflow-y:auto;display:inline-flex;font-size:1.4em;cursor:initial}.dialog>.dialog__inner>.stack{--space:.8rem;width:100%;align-items:stretch}.dialog__content{overflow:hidden;margin-bottom:auto;flex-grow:1}.dialog__content .config{padding:.1em!important}.dialog__close{position:absolute;right:.2em;top:.15em;font-size:3em;color:#000!important}.dialog__title{padding-right:1.5em;margin-bottom:.25em;font-size:2.2em;line-height:.8}@media (max-width:768px){.dialog__title{font-size:2em}}.dialog-footer{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;margin:-.35em}.dialog-footer>*{margin:.35em}.dialog-footer-wrap{margin-left:auto;margin-right:auto}