.numeric-input{width:100%;font-size:1em}.numeric-input-wrap{position:relative}.numeric-input-wrap .ant-input,.numeric-input-wrap .ant-input-affix-wrapper{text-align:inherit}.numeric-input-wrap button{position:absolute;margin:0;padding:0;border:0;border-radius:50%;background-color:rgba(0,0,0,.15);height:1em;width:1em;right:.4rem;top:50%;transform:translateY(-50%);font-size:1.4em}.numeric-input-wrap button svg{position:absolute;left:50%;top:50%;transform:translate(-50%,-50%) rotate(90deg);margin-left:.01em;height:.35em;width:.35em;fill:green}.numeric-input-wrap button svg.negative{transform:translate(-50%,-50%) rotate(-90deg);fill:red}