.cross-button{position:relative;display:inline-flex;justify-content:center;align-items:center;background:0 0;border:0;border-radius:50%;padding:0;width:1em;height:1em;color:#fff;line-height:1;cursor:pointer;transition:transform .15s ease-in-out;will-change:transform}.cross-button:focus,.cross-button:hover{background-color:rgba(0,0,0,.05)}.cross-button button:disabled{background:0 0;border:0;padding:0}.cross-button [role=img]{font-size:.625em;transform:rotate(45deg);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}